import Vue from "vue";

export const state = Vue.observable({
  activeFeatures: [],
  routesCache: {},
  routeColor: {
    cycling: "#AAE08F",
    car: "#67000D",
    walking: "#2E62DB",
    rail: "#EC7158",
    pt: "#EC7158",
    pt_walking: "#EC7158"
  },
  ptDetailIcons: {
    WALKING: { icon: "directions_walk", category: "pt_walking" },
    HEAVY_RAIL: { icon: "train", category: "rail" },
    COMMUTER_RAIL: { icon: "train", category: "rail" },
    COMMUTER_TRAIN: { icon: "train", category: "rail" },
    HIGH_SPEED_TRAIN: { icon: "train", category: "rail" },
    TRAM: { icon: "tram", category: "rail" },
    BUS: { icon: "directions_bus", category: "rail" }
  },
  panel: [0]
});

export const mutations = {
  addActiveFeature(feature) {
    let gate;
    let mode;
    let start;
    let keyName;
    let obj;
    if (Array.isArray(feature)) {
      start = feature[0].get("start");
      mode = feature[0].get("mode");
      gate = feature[0].get("gate");
      keyName = `${start}__${mode}`;
      if (mode === "car") {
        keyName += `__${feature[0].get("traffic_time")}`;
      }
      obj = feature;
    } else {
      gate = feature.gate;
      mode = feature.mode;
      start = feature.start;
      keyName = `${start}__${mode}`;
      obj = "route_not_available";
    }

    const index = state.activeFeatures.findIndex(x => x.name === keyName);
    if (index !== -1) {
      //Using Vue.set due to reactivity problems in Vue.js version 2
      if (obj !== "route_not_available") {
        Vue.set(state.activeFeatures[index].data, gate, feature);
      }
    } else {
      state.activeFeatures.push({
        name: keyName,
        mode: mode,
        data:
          obj === "route_not_available"
            ? "route_not_available"
            : {
                [gate]: obj
              }
      });
    }

    state.activeFeatures.sort((a, b) => {
      let aName = a.name.split("__")[0];
      let bName = b.name.split("__")[0];
      if (aName === bName && a.mode === "pt" && b.mode === "cycling") {
        return -1;
      }
      if (aName === bName && a.mode === "cycling" && b.mode === "pt") {
        return 1;
      }
      return a.name > b.name ? 1 : -1;
    });
  },
  clearActiveFeatures() {
    state.activeFeatures = [];
  },
  addRoutesInCache(name, olFeatures) {
    state.routesCache[name] = olFeatures;
  },
  panelChanged(value) {
    state.panel = value;
  }
};
