<template>
  <v-footer height="40" class="font-weight-medium">
    <v-btn small icon @click="changeLocale()">
      <v-img :src="flag[languageSelected]" contain></v-img>
    </v-btn>
    <v-spacer></v-spacer>
    <a
      class="footer"
      href="https://www.mainsite.de/de/industrie-center-obernburg.html"
      target="_blank"
    >
      &copy; {{ new Date().getFullYear() }} ICO
    </a>
  </v-footer>
</template>

<script>
import i18n from "@/plugins/i18n";
import { EventBus } from "../../EventBus";

export default {
  data() {
    return {
      flag: {
        de: require("../../assets/img/de.png"),
        en: require("../../assets/img/gb.png")
      }
    };
  },
  computed: {
    languageSelected() {
      const languageSelected = i18n.locale;
      EventBus.$emit("language-selected", languageSelected);
      if (languageSelected === "de") {
        return "en";
      } else {
        return "de";
      }
    }
  },
  methods: {
    changeLocale() {
      if (i18n.locale === "de") {
        i18n.locale = "en";
      } else {
        i18n.locale = "de";
      }
    }
  }
};
</script>
<style scoped>
.footer {
  text-decoration: none;
  font-size: 14px;
  font-family: Roboto;
  font-weight: bold;
}
</style>
