        <v-expansion-panels v-model="panel" accordion multiple>
          <v-expansion-panel>
            <v-expansion-panel-header v-slot="{ open }">
              <v-layout row wrap align-center>
                <v-flex xs1>
                  <v-icon :class="open ? 'active-header' : ''" large
                    >directions</v-icon
