<script>
import Selection from "./Selection";
import Results from "./Results";
import Footer from "./Footer";
import { state, mutations } from "../../store";

export default {
  components: {
    Selection,
    Results,
    Footer
  },
  props: ["controlsColor"],
  computed: {
    panel: {
      // getter
      get: function() {
        return state.panel;
      },
      // setter
      set: function(newValue) {
        this.panelChanged(newValue);
      }
    }
  },
  methods: {
    panelChanged: mutations.panelChanged
  }
};
</script>

