<template>
  <v-list>
    <template v-for="(item, index) in activeFeatures">
      <v-list-item :key="index" class="pr-0">
        <template>
          <v-list-item-avatar left class="mr-4">
            <v-icon large v-text="getAvatarIcon(item)"></v-icon>
          </v-list-item-avatar>
          <v-list-item-content class="text-left">
            <span class="subtitle-2" v-text="getTitle(item)"></span>
            <template
              v-if="item.mode === 'pt' && item.data !== 'route_not_available'"
            >
              <span>
                <template
                  class="mt-2"
                  v-for="(value, key, index) in item.data.tor2"
                >
                  <span :key="index" style="margin-bottom:2px;">
                    <i
                      class="v-icon notranslate material-icons mr-1 theme--light"
                      style="font-size: 20px;"
                    >
                      {{ ptDetailIcons[value.get("transit_type")].icon }}</i
                    >
                    <span></span>

                    <div
                      class="pt-rectangle mr-1"
                      @mouseover="mouseover(value)"
                      @mouseleave="clearHighlight()"
                      @click="clicked(value)"
                      :style="[
                        ptDetailIcons[
                          value.get('transit_type').category !== 'pt_walking'
                        ]
                          ? {
                              'background-color':
                                routeColor[
                                  ptDetailIcons[value.get('transit_type')]
                                    .category
                                ]
                            }
                          : ''
                      ]"
                      :class="{
                        'pt-dotted':
                          ptDetailIcons[value.get('transit_type')].category ===
                          'pt_walking',
                        'pt-solid':
                          ptDetailIcons[value.get('transit_type')].category !==
                          'pt_walking'
                      }"
                    ></div>

                    <span> </span>
                  </span>
                </template>
                <span class="ml-1">
                  <i
                    style="font-size: 18px;margin-bottom:4px;"
                    class="fas fa-industry v-icon notranslate material-icons theme--light"
                  ></i>
                </span>
              </span>
            </template>
          </v-list-item-content>
          <v-list-item-action
            :class="{ 'ml-1': item.mode === 'pt' }"
            class="my-0 py-0"
            v-if="item.data !== 'route_not_available'"
          >
            <table class="detail-gate" v-if="item.mode !== 'pt'">
              <template v-for="(value, key, index2) in item.data">
                <tr
                  :key="index2"
                  @mouseover="mouseover(value)"
                  @mouseleave="clearHighlight()"
                >
                  <td>
                    <a @click="clicked(value)">{{ $t(`results.${key}`) }}</a>
                  </td>
                  <td
                    :style="{
                      'background-color': routeColor[value[0].get('mode')]
                    }"
                    style="minWidth: 149px;cursor:pointer;"
                    class="detail-gate-rectangle"
                  >
                    <span>{{ getTimeAndDistance(value) }}</span>
                  </td>
                </tr>
              </template>
            </table>
            <template
              v-else-if="
                item.mode === 'pt' && item.data !== 'route_not_available'
              "
            >
              <v-tooltip top>
                <template v-slot:activator="{ on }">
                  <span
                    v-on="on"
                    class="subtitle-2"
                    style="text-align:right;padding-right:10px;cursor:pointer;"
                    >{{ getTransitTotalDistance(item) }}<br />{{
                      getTransitTotalTime(item)
                    }}</span
                  >
                </template>
                <span>{{ $t("tooltips.waitingTime") }}</span>
              </v-tooltip>
            </template>
          </v-list-item-action>
        </template>
      </v-list-item>
      <v-divider
        v-if="index + 1 < activeFeatures.length"
        :key="index + 3000"
      ></v-divider>
    </template>
  </v-list>
</template>

<script>
import { state } from "../../store";
import { Mapable } from "../../mixins/Mapable";
import { secondsToHm } from "../../utils/Helpers";

import OlStyleDefs from "../../style/OlStyleDefs";

//Ol imports
import VectorSource from "ol/source/Vector";
import VectorLayer from "ol/layer/Vector";

export default {
  mixins: [Mapable],
  data: () => ({
    endPointName: "",
    icon: {
      walking: "directions_walk",
      cycling: "directions_bike",
      pt: "directions_transit",
      car: "directions_car"
    },
    routes: []
  }),
  computed: {
    routeColor() {
      return state.routeColor;
    },
    activeFeatures() {
      return state.activeFeatures;
    },
    ptDetailIcons() {
      return state.ptDetailIcons;
    }
  },
  methods: {
    onMapBound() {
      this.createHightLightLayer();
    },
    createHightLightLayer() {
      const me = this;
      const vector = new VectorLayer({
        name: "Highlight Layer",
        style: OlStyleDefs.getFeatureHighlightStyle(),
        zIndex: 4,
        source: new VectorSource()
      });
      me.map.addLayer(vector);
      this.highlightLayer = vector;
    },
    getAvatarIcon(item) {
      const icon = this.icon[item.name.split("__")[1]];
      return icon;
    },
    getTitle(item) {
      let title = `${this.$t(`startingPoints.${item.name.split("__")[0]}`)}`;
      if (item.mode === "car") {
        title =
          `(${this.$t("selection." + item.name.split("__")[2])}) ` + title;
      }
      if (item.data === "route_not_available") {
        title += ` (${this.$t("results.route_not_available")})`;
      }
      return title;
    },

    getTimeAndDistance(item) {
      let summary;
      const mode = item[0].get("mode");

      if (mode === "car") {
        summary = {
          duration: item[0].get("duration").value,
          distance: item[0].get("distance").value
        };
      } else {
        summary = item[0].get("summary");
      }

      const convertedTime = secondsToHm(summary.duration);
      return `${convertedTime} (${(summary.distance / 1000).toFixed(1)} km)`;
    },
    getTransitTotalTime(item) {
      const time = item.data.tor2[0].get("total_traveltime");
      const splitReversed = time.split(":").reverse();
      let totalInHours = 0;
      splitReversed.forEach((time, index) => {
        let timeNumber = parseInt(time, 10);
        if (index === 0) {
          //Seconds.
          totalInHours = totalInHours + timeNumber / 3600;
        } else if (index === 1) {
          //Minutes
          totalInHours = totalInHours + timeNumber / 60;
        } else if (index === 2) {
          //Hours
          totalInHours = totalInHours + timeNumber;
        }
      });
      return `${secondsToHm(totalInHours * 3600)}`;
    },
    getTransitTotalDistance(item) {
      const distance = `${(
        item.data.tor2[0].get("summary").distance / 1000
      ).toFixed(1)} km`;
      return distance;
    },
    addHighlight(features) {
      if (Array.isArray(features)) {
        features.forEach(feature => {
          this.highlightLayer.getSource().addFeature(feature.clone());
        });
      } else {
        //For public transport
        this.highlightLayer.getSource().addFeature(features.clone());
      }
    },
    clearHighlight() {
      this.highlightLayer.getSource().clear();
    },
    mouseover(features) {
      this.addHighlight(features);
    },
    clicked(features) {
      this.addHighlight(features);
      this.map.getView().fit(this.highlightLayer.getSource().getExtent());
      setTimeout(() => {
        this.clearHighlight();
      }, 1000);
    }
  }
};
</script>
<style lang="css" scoped>
.v-list {
  padding: 2px 0;
}

.detail-gate {
  text-decoration: none;
  font-size: 13px;
  font-family: Roboto;
  font-weight: bold;
}

.detail-gate-rectangle {
  color: white;
  padding-left: 4px;
  border-radius: 4px 0px 0px 4px;
}

.pt-solid {
  min-height: 10px;
  cursor: pointer;
  background-color: rgb(236, 113, 88);
}

.pt-dotted {
  cursor: pointer;
  border-top: 4px dotted rgb(236, 113, 88);
}
</style>
