import OlStyle from "ol/style/Style";
import OlStroke from "ol/style/Stroke";
import OlFill from "ol/style/Fill";
import OlCircle from "ol/style/Circle";
import OlIcon from "ol/style/Icon";
import OlText from "ol/style/Text";
import Point from "ol/geom/Point";
import { state } from "../store";
import { shadeColor } from "../utils/Helpers";
import i18n from "../plugins/i18n";

let routeStyleCache = {};

const OlStyleDefs = {
  boundaryStyle: () => {
    return new OlStyle({
      fill: new OlFill({
        color: [0, 0, 0, 0]
      }),
      stroke: new OlStroke({
        color: "#707070",
        width: 5.5
      })
    });
  },
  getSelectStyle: () => {
    return new OlStyle({
      fill: new OlFill({
        color: [0, 0, 0, 0]
      }),
      stroke: new OlStroke({
        color: "#fe4a49",
        width: 5,
        lineDash: [10, 10]
      })
    });
  },
  defaultStyle: () => {
    const style = new OlStyle({
      fill: new OlFill({
        color: [0, 0, 0, 0]
      }),
      stroke: new OlStroke({
        color: "#707070",
        width: 3
      })
    });
    return [style];
  },

  getFeatureHighlightStyle: () => {
    return [
      new OlStyle({
        fill: new OlFill({
          color: [0, 0, 0, 0]
        }),
        stroke: new OlStroke({
          color: "#FF0000",
          width: 10
        }),
        image: new OlCircle({
          radius: 10,
          fill: new OlFill({
            color: "#FF0000"
          })
        })
      })
    ];
  },
  getGatesStyle: () => {
    const styleFunction = feature => {
      let styles = [];
      let iconStyle = new OlStyle({
        image: new OlIcon({
          anchor: [0.5, 0.9],
          src: "static/marker-24.png",
          scale: 1
        }),
        text: new OlText({
          offsetX: 40,
          offsetY: -22,
          font: "bold 14px Roboto",
          fill: new OlFill({ color: "#fff" }),
          backgroundFill: new OlFill({
            color: "rgb(255, 0, 0)"
          }),
          padding: [3, 5, 2, 4],
          text: i18n.t(`results.${feature.get("name")}`)
        })
      });

      styles.push(iconStyle);
      return styles;
    };

    return styleFunction;
  },
  getRoutesStyle: () => {
    const styleFunction = feature => {
      //Styles array
      let styles = [];
      const mode = feature.get("mode");
      let color = [0, 0, 0, 0];
      let key = `line_${mode}_${color}_`;
      if (mode === "pt") {
        color =
          state.routeColor[
            state.ptDetailIcons[feature.get("transit_type")].category
          ];
        key += `${state.ptDetailIcons[feature.get("transit_type")].category}`;
      } else {
        color = state.routeColor[mode];
      }

      let lineStyle = routeStyleCache[key];

      if (!lineStyle) {
        lineStyle = [
          new OlStyle({
            stroke: new OlStroke({
              color: shadeColor(color, 0),
              width: 9,
              lineDash:
                mode === "pt" &&
                state.ptDetailIcons[feature.get("transit_type")].category ===
                  "pt_walking"
                  ? [0.1, 12]
                  : undefined
            })
          })
        ];
        routeStyleCache[`line_` + color] = lineStyle;
      }

      styles.push(...lineStyle);

      const firstCoord = feature.getGeometry().getFirstCoordinate();
      const lastCoord = feature.getGeometry().getLastCoordinate();
      if (
        mode === "pt" &&
        state.ptDetailIcons[feature.get("transit_type")].category === "rail"
      ) {
        let key = `pt_rail_${firstCoord[0]}_${firstCoord[1]}_${feature.get(
          "transit_type"
        )}`;
        let railStationPointStyle = routeStyleCache[key];
        if (!railStationPointStyle) {
          railStationPointStyle = new OlStyle({
            geometry: new Point([firstCoord[0], firstCoord[1]]),
            image: new OlIcon({
              src: `static/${
                state.ptDetailIcons[feature.get("transit_type")].icon
              }.png`,
              anchor: [0.5, 0.9],
              scale: 1
            })
          });
          routeStyleCache[key] = railStationPointStyle;
        }
        styles.push(railStationPointStyle);
      } else {
        let startPointKey = `start_point_${firstCoord[0]}_${firstCoord[1]}`;
        let startPointStyle = routeStyleCache[startPointKey];
        if (!startPointStyle) {
          startPointStyle = new OlStyle({
            geometry: new Point([firstCoord[0], firstCoord[1]]),
            image: new OlCircle({
              radius: 6,
              fill: new OlFill({
                color: "#FFFFFF"
              }),
              stroke: new OlStroke({
                color: "#000000",
                width: 3
              })
            })
          });
          routeStyleCache[startPointKey] = startPointStyle;
        }
        styles.push(startPointStyle);
      }

      let endpointkey = `end_point_${lastCoord[0]}_${lastCoord[1]}`;
      let endPointStyle = routeStyleCache[endpointkey];
      if (!endPointStyle) {
        endPointStyle = new OlStyle({
          geometry: new Point([lastCoord[0], lastCoord[1]]),
          image: new OlCircle({
            radius: 6,
            fill: new OlFill({
              color: "#FFFFFF"
            }),
            stroke: new OlStroke({
              color: "#000000",
              width: 3
            })
          })
        });
        routeStyleCache[endpointkey] = endPointStyle;
      }
      styles.push(endPointStyle);

      return styles;
    };
    return styleFunction;
  }
};

export default OlStyleDefs;
