<template>
  <v-app
    id="app"
    data-app
    :class="{ 'wg-app': true, 'wg-app-embedded': isEmbedded }"
  >
    <v-navigation-drawer
      class="hidden-sm-and-down left-shadow"
      right
      :app="!$vuetify.breakpoint.smAndDown"
      absolute
      permanent
      stateless
      width="400"
    >
      <panel :controlsColor="controlsColor" />
    </v-navigation-drawer>

    <v-app-bar
      v-if="$vuetify.breakpoint.smAndDown"
      :color="controlsColor"
      app
      dark
    >
      <v-btn icon @click="bottomSheet = !bottomSheet"
        ><v-icon medium>{{ bottomSheet ? "$close" : "$menu" }}</v-icon></v-btn
      >

      <v-toolbar-title> {{ $t("appTitle") }}</v-toolbar-title>
    </v-app-bar>

    <v-bottom-sheet
      v-model="bottomSheet"
      no-click-animation
      hide-overlay
      persistent
      v-if="$vuetify.breakpoint.smAndDown"
    >
      <v-sheet class="text-center" height="60vh">
        <panel :controlsColor="controlsColor" />
      </v-sheet>
    </v-bottom-sheet>

    <v-content>
      <v-container id="ol-map-container" fluid fill-height class="pa-0">
        <app-map :color="controlsColor" />
        <background-switcher />
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import Vue from "vue";
import { EventBus } from "./EventBus.js";
import appMap from "./components/ol/Map";
import Panel from "./components/core/Panel";
import BackgroundSwitcher from "./components/ol/BackgroundSwitcher";

export default {
  name: "wg-app",
  components: {
    appMap,
    BackgroundSwitcher,
    Panel
  },
  data() {
    return {
      bottomSheet: false,
      isEmbedded: false,
      controlsColor: Vue.prototype.$appConfig.controlsColor
    };
  },
  mounted() {
    // apply the isEmbedded state to the member var
    this.isEmbedded = this.$isEmbedded;
    // inform registered cmps that the app is mounted and the dynamic
    // components are available
    EventBus.$emit("app-mounted");
  }
};
</script>
<style>
.left-shadow {
  box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 20px;
}
</style>
