<template>
  <div>
    <v-select
      v-model="startPoints.actives"
      :items="startPoints.values"
      :item-text="startingPointDisplayText"
      item-value="value"
      chips
      @change="changed"
      deletable-chips
      :label="$t('selection.startPoints')"
      prepend-icon="room"
      multiple
    >
    </v-select>

    <v-select
      v-model="transportModes.actives"
      :items="transportModes.values"
      :item-text="transportModesDisplayText"
      item-value="value"
      chips
      @change="changed"
      deletable-chips
      :label="$t('selection.transportModes')"
      prepend-icon="emoji_transportation"
      multiple
    >
      <template v-slot:selection="data">
        <v-chip
          :key="JSON.stringify(data.item.value)"
          v-bind="data.attrs"
          dark
          :color="routeColor[data.item.value]"
          close
          :input-value="data.selected"
          :disabled="data.disabled"
          @click:close="data.parent.selectItem(data.item.value)"
        >
          <v-icon left>{{ data.item.icon }}</v-icon>
          {{ $t(`transportModes.${data.item.value}`) }}
        </v-chip>
      </template>
    </v-select>
    <v-layout row wrap align-center>
      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-checkbox
            v-model="peak"
            class="ml-3"
            color="#1976d2"
            @change="changed"
          >
            <template slot="prepend">
              <v-icon>access_time</v-icon>
              <span class="v-label theme--light ml-1 mr-2">{{
                $t("selection.time")
              }}</span>
            </template>
            <span v-on="on" slot="label">{{ $t("selection.peak") }}</span>
          </v-checkbox>
        </template>
        <span>{{ $t("tooltips.onPeak") }}</span>
      </v-tooltip>

      <v-tooltip top>
        <template v-slot:activator="{ on }">
          <v-checkbox
            v-model="offPeak"
            color="#1976d2"
            class="ml-3"
            @change="changed"
          >
            <span v-on="on" slot="label">{{ $t("selection.offPeak") }}</span>
          </v-checkbox>
        </template>
        <span>{{ $t("tooltips.offPeak") }}</span>
      </v-tooltip>
    </v-layout>
  </div>
</template>

<script>
import { geojsonToFeature } from "../../utils/MapUtils";
import { Mapable } from "../../mixins/Mapable";
import OlStyleDefs from "../../style/OlStyleDefs";
import { EventBus } from "../../EventBus";

//Ol imports
import VectorSource from "ol/source/Vector";
import VectorLayer from "ol/layer/Vector";

import { mutations, state } from "../../store";

export default {
  mixins: [Mapable],
  data: () => ({
    peak: true,
    offPeak: false,
    transportModes: {
      values: [
        { display: "Auto", value: "car", icon: "directions_car" },
        {
          display: "Öffentlicher Verkehr",
          value: "pt",
          icon: "directions_transit"
        },
        { display: "Zu Fuß", value: "walking", icon: "directions_walk" },
        { display: "Fahrrad", value: "cycling", icon: "directions_bike" }
      ],
      actives: []
    },
    startPoints: {
      values: [
        { display: "aschaffenburg_hbf", value: "Aschaffenburg Hbf" },
        { display: "darmstadt_hbf", value: "Darmstadt Hbf" },
        { display: "elsenfeld", value: "Elsenfeld" },
        { display: "erlenbach", value: "Erlenbach" },
        {
          display: "frankfurt_am_flughafen",
          value: "Frankfurt a.M. Flughafen"
        },
        { display: "frankfurt_am_Hbf", value: "Frankfurt a.M. Hbf" },
        { display: "freudenberg", value: "Freudenberg" },
        { display: "klingenberg", value: "Klingenberg" },
        { display: "ludwigshafen_hbf", value: "Ludwigshafen Hbf" },
        { display: "ludwigshafen_zentrum", value: "Ludwigshafen Zentrum" },
        { display: "mannheim_bahnhof", value: "Mannheim Bahnhof" },
        { display: "mannheim_zentrum", value: "Mannheim Zentrum" },
        { display: "miltenberg", value: "Miltenberg" },
        { display: "obernburg", value: "Obernburg" },
        { display: "wurzburg_hbf", value: "Würzburg Hbf" }
      ],
      actives: []
    },
    gates: {
      walking: ["tor1", "tor2", "tor4"],
      cycling: ["tor1", "tor2", "tor4"],
      car: ["tor1", "tor2", "tor4"],
      pt: ["tor2"]
    },
    hasTime: ["car"]
  }),
  created() {
    EventBus.$on("language-selected", () => {
      if (this.gatesLayer) {
        this.gatesLayer.getSource().changed();
      }
    });
  },
  computed: {
    routeColor() {
      return state.routeColor;
    }
  },
  methods: {
    onMapBound() {
      this.createRouteLayer();
      this.createGatesLayer();
    },
    startingPointDisplayText: function(item) {
      return this.$t(`startingPoints.${item.display}`);
    },
    transportModesDisplayText: function(item) {
      return this.$t(`transportModes.${item.value}`);
    },
    addActiveFeature: mutations.addActiveFeature,
    clearActiveFeatures: mutations.clearActiveFeatures,
    addRoutesInCache: mutations.addRoutesInCache,
    panelChanged: mutations.panelChanged,
    createRouteLayer() {
      const me = this;
      const vector = new VectorLayer({
        name: "Routes Layer",
        zIndex: 3,
        style: OlStyleDefs.getRoutesStyle(),
        source: new VectorSource()
      });
      me.map.addLayer(vector);
      this.routeLayer = vector;
    },
    createGatesLayer() {
      const me = this;
      const vector = new VectorLayer({
        name: "Gates Layer",
        zIndex: 6,
        style: OlStyleDefs.getGatesStyle(),
        source: new VectorSource()
      });
      me.map.addLayer(vector);
      this.gatesLayer = vector;
      this.fetchGates();
    },
    changed() {
      const me = this;

      me.clearActiveFeatures();
      me.routeLayer.getSource().clear();
      me.startPoints.actives.forEach(startPoint => {
        me.transportModes.actives.forEach(mode => {
          if (!me.gates[mode]) return;

          me.gates[mode].forEach(gate => {
            let name = `${startPoint}_${gate}_${mode}`;
            if (state.panel.length < 2) {
              me.panelChanged([0, 1]);
            }
            if (me.hasTime.includes(mode)) {
              if (this.peak) {
                me.load(`${name}_peak`, mode, startPoint, gate);
              }
              if (this.offPeak) {
                me.load(`${name}_off_peak`, mode, startPoint, gate);
              }
            } else {
              me.load(name, mode, startPoint, gate);
            }
          });
        });
      });
    },
    load(name, mode, startPoint, gate) {
      const me = this;
      //Check if route is already loaded

      if (!state.routesCache[name]) {
        me.fetchFile(name, mode, startPoint, gate);
      } else {
        me.addActiveFeature(state.routesCache[name]);
        me.routeLayer.getSource().addFeatures(state.routesCache[name]);
        me.map.getView().fit(this.routeLayer.getSource().getExtent());
      }
    },
    fetchFile(name, mode, start, gate) {
      const me = this;

      let displayName = me.startPoints.values.find(
        ({ value }) => value === start
      ).display;

      return fetch("static/routes/" + name + ".geojson")
        .then(function(response) {
          return response.json();
        })
        .then(function(json) {
          let olFeatures = geojsonToFeature(json, {
            dataProjection: "EPSG:4326",
            featureProjection: "EPSG:3857"
          });

          //Add mode property foreach feature, which is used for styling later
          olFeatures.forEach(feature => {
            feature.setProperties({
              mode: mode,
              start: displayName,
              gate: gate
            });
          });

          if (mode === "pt") {
            olFeatures = olFeatures.filter(
              feature => feature.get("duration").value > 60
            );

            let summary = {
              distance: 0
            };
            olFeatures.forEach(feature => {
              summary.distance += feature.get("distance").value;
            });

            olFeatures.map(feature => {
              feature.setProperties({ summary });
              return feature;
            });
          }

          if (olFeatures.length > 1 && olFeatures[0].get("step")) {
            olFeatures.sort((a, b) => {
              return a.get("step") - b.get("step");
            });
          }
          me.addRoutesInCache(name, olFeatures);
          me.addActiveFeature(olFeatures);
          me.routeLayer.getSource().addFeatures(olFeatures);
          me.map.getView().fit(me.routeLayer.getSource().getExtent());
        })
        .catch(function() {
          if (mode === "walking" || mode === "cycling") {
            me.addActiveFeature({
              mode: mode,
              start: displayName,
              gate: gate
            });
          }
        });
    },
    fetchGates() {
      const me = this;
      fetch("static/gates.geojson")
        .then(function(response) {
          return response.json();
        })
        .then(function(json) {
          let olFeatures = geojsonToFeature(json, {
            dataProjection: "EPSG:4326",
            featureProjection: "EPSG:3857"
          });
          me.gatesLayer.getSource().addFeatures(olFeatures);
        });
    }
  }
};
</script>
